#landing-section p {
  margin-top: 1rem;
}

#landing-section > div {
  width: 50%;
}

#landing-section > div:last-child > div {
  height: 25rem;
  width: 40rem;
  border: 1px solid white;
}

@media only screen  and (max-width: 769px){
  #landing-section > div {
    width: 100%;
  }

  #landing-section {
    justify-content: center;
  }
  
  #landing-section > div:last-child > div {
    display: none;
  }
}
